.login{display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;-webkit-box-pack:center;justify-content:center;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column;min-height:calc(100vh - 200px);border:2px #F0544F solid}.container{padding:20px;border:2px #F0544F solid;border-radius:20px;width:250px}.login__container form{display:-webkit-box;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}.input{background-color:transparent;border:none;border-bottom:2px #F0544F solid;margin-bottom:30px;padding:0 0 10px 20px;outline:none}.button{background-color:#F0544F;color:white;padding:12px;border:none;border-radius:20px}.button:hover{background-color:#ad3f4c}.recovery{margin:25px 0 10px 0;display:-webkit-box;display:flex;-webkit-box-pack:end;justify-content:flex-end}.recovery a{text-decoration:none;color:black}.recovery a:hover{text-decoration:underline}.social img{width:40px;background-color:red}
